import { ID, Services } from '@realmocean/sdk'
import config from './config'

const uniqueIds = {
  organisationId: ID.unique(),
  projectId: ID.unique(),
}

export async function createProjectNewVersion(
  projectName: string,
  projectDescription: string,
  organisationName: string,
  databaseName: string
) {
  const Database = 'rnekDB_' + uniqueIds.projectId // Benzersiz bir veritaban ad olutur

  var organization
  try {
    organization = await Services.Teams.create(
      uniqueIds.organisationId,
      organisationName
    )
    console.log('yeni oluturulan organizasyon: ', organization)
  } catch (e) {
    organization = await Services.Teams.get(uniqueIds.organisationId)
    console.log('organizasyon zaten var, olan organizasyon: ', organization)
  }
  if (!organization) return

  Services.Accounts.updatePrefs({ organization: organization.$id })

  var realm
  try {
    realm = await Services.Projects.create(
      uniqueIds.projectId,
      projectName,
      organization.$id
    ) //proje olutur, AppInfo.Name, verilen projectId deeri
    console.log('yeni oluturulan proje: ', realm)
  } catch (e) {
    //proje zaten varsa
    console.log('proje oluturulamad error: ', e)
    realm = await Services.Projects.get(projectName)
  }

  if (!realm) return

  Services.Client.setMode('admin')

  var database
  try {
    database = await Services.Databases.create(
      realm.$id,
      databaseName, //databaseid, unique te olabilir burda farketmez pek diye isimle ayn yapld
      databaseName //database ad
    )
  } catch (e) {
    console.log('database oluturulamad error: ', e)
    database = await Services.Databases.get(realm.$id, databaseName)
  }
  if (!database) return

  //elde ettiin database objesi ile eklemeler karmalar yapacaksn daha sonra
  // Services.Databases.createCollection() ile tablolar oluturabilirsi

  //Kodlar bu araya yazcan aada istediin eyleri return edicen
  //Kodlar bu araya yazcan aada istediin eyleri return edicen
  //Kodlar bu araya yazcan aada istediin eyleri return edicen

  return {
    organization,
    realm,
    database,
  }
}

export async function getProjects() {
  try {
    const response = await Services.Databases.listCollections(
      config.databaseId,
      config.databaseName
    )
    console.log('Projeler ekildi:', response)
    return response.collections
  } catch (error) {
    console.error('Projeleri ekme hatas:', error)
    throw error
  }
}

export async function createNewProject(
  projectId: string,
  projectName: string,
  teamId: string
) {
  try {
    return await Services.Projects.create(projectId, projectName, teamId)
  } catch (e) {
    console.log(e)
  }
}

export class DatabaseSetup {
  async createOrganization(name: string) {
    const organization = await Services.Teams.create(ID.unique(), name)
    console.log('Organizasyon Oluturuldu: ', organization.name)
    return organization.$id
  }

  async createDatabase(organizationId: string, dbName: string) {
    const database = await Services.Databases.create(
      organizationId,
      dbName,
      dbName
    )
    console.log('Veritaban Oluturuldu: ', database.name)
    return database.$id
  }

  async createCollection(
    databaseId: string,
    collectionName: string,
    description: string,
    additionalConfig: string
  ) {
    const collection = await Services.Databases.createCollection(
      databaseId,
      collectionName,
      description,
      additionalConfig
    )
    console.log('Koleksiyon Oluturuldu: ', collection.name)
    return collection.$id
  }
}
